
.setup-container {
  padding: 1.5rem;
  min-height: 100%;
}

.setup-card {
  max-width: 900px;
  margin: 0 auto;
  background: var(--c-bg-card);
  border: 1px solid var(--c-border);
  border-radius: 1rem;
  overflow: hidden;
}

.setup-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  background: linear-gradient(135deg, var(--c-primary-dark), var(--c-primary));
  border-bottom: 1px solid var(--c-border);
}

.setup-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: white;
  margin: 0;
}

.setup-content {
  padding: 1.5rem;
}

.tab-content {
  margin-top: 1.5rem;
}

.tab-panel {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.stat-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.stat-value {
  color: var(--c-primary-light);
  font-weight: 600;
}

.span-2 {
  grid-column: span 2;
}

.checkbox-group {
  display: flex;
  align-items: center;
  padding-top: 1.5rem;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  color: var(--c-text);
}

.form-checkbox {
  width: 1.25rem;
  height: 1.25rem;
  accent-color: var(--c-primary);
  cursor: pointer;
}

.loading {
  text-align: center;
  padding: 3rem;
  color: var(--c-text-muted);
}

@media (max-width: 640px) {
  .setup-container {
    padding: 0.75rem;
  }

  .setup-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .span-2 {
    grid-column: span 1;
  }
}
